.our-projects {
  max-width: 1170px;
  margin: 0 auto;
  margin-top: 63px;
  margin-bottom: 120px;
}

.our-projects__title {
  font-size: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
}

.our-projects__subtitle {
  font-size: 64px;
  font-weight: 700;
  color: #333333;
  margin: 0;
}

.our-projects__samples {
  border-top: 1px solid #f2f2f2;
  margin-top: 30px;
}

.our-projects__item {
  display: flex;
  margin-top: 30px;
  margin-bottom: 60px;
  background-color: #fbfbfb;
}

.our-projects__image {
  width: 670px;
}

.our-projects__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 30px;
}

.card__title {
  font-size: 40px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
  margin-bottom: 10px;
}

.card__text {
  font-size: 16px;
  line-height: 25px;
  font-weight: 300;
  margin: 0;
  width: 400px;
}

.card__button {
  width: 220px;
  height: 70px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 40px;
  padding-right: 50px;
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
  color: #333333;
}
