.sample-project-1 {
  max-width: 1170px;
  margin: 0 auto;
  margin-top: 63px;
  margin-bottom: 120px;
}

.sample-project-1__title {
  font-size: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
}

.sample-project-1__subtitle {
  font-size: 64px;
  font-weight: 700;
  color: #333333;
  margin: 0;
}

.sample-project-1__item {
  display: grid;
  grid-template-columns: 420px 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 30px;
  border-top: 1px solid #f2f2f2;
  padding-top: 30px;
  margin-top: 30px;
}

.sample-project-1__image-1 {
  grid-column: 1 / -1;
  grid-row: 1 / 2;
}

.sample-project-1__image-2 {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
}

.sample-project-1__image-3 {
  grid-column: 1 / -1;
  grid-row: 3 / 4;
}

.sample-project-1__text {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  font-size: 16px;
  line-height: 25px;
  font-weight: 300;
  margin: 0;
}
