.header {
  max-width: 1170px;
  margin: 0 auto;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 102px;
}

.header__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 636px;
  margin-right: 270px;
}

.header__logo {
  width: 70px;
  height: 44px;

}

.header__item {
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
}

.header__item:hover {
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
}

.header__link {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  text-transform: uppercase;
  color: #333333;
}


