.footer {
  background-color: #2c2c2c;
  color: #ffffff;
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 1fr 260px 415px 1fr;
  padding-top: 58px;
}

.footer__logo {
  padding: 0 100px;
}

.footer__title {
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  margin-bottom: 25px;
}

.info__item {
  margin-bottom: 16px;
}

.info-link {
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
}

.info-link:hover {
  opacity: 0.7;
}

.contacts__item {
  display: grid;
  grid-template-columns: 30px 1fr;
  margin-bottom: 32px;
  align-items:center;
}

.contacts__link {
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
  margin-top: 0;
}

.contacts__link:hover {
  opacity: 0.7;
}

.contacts__marker  {
  align-self: flex-start;
}

.social__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 190px;
}

.footer__copyright {
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  line-height: 22px;
  font-weight: 400;
  color: #c8c8c8;
  opacity: 50%;
  text-align: center;
  vertical-align: middle;
  height: 70px;
  border-top: 1px solid #ffffff;
  padding-top: 10px;
}
