.information {
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-bottom: 120px;
}

.information__item {
  width: 650px;
  margin-top: 63px;
}

.information__title {
  font-size: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
}

.information__subtitle {
  font-size: 64px;
  font-weight: 700;
  color: #333333;
  margin: 0;
}

.information__adress {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  margin-top: 34px;
}

.information__span {
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
}

.information__phone {
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  margin-top: 44px;
}

.information__email {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  margin-top: 53px;
  margin-bottom: 20px;
}

.information__button {
  width: 220px;
  height: 70px;
  background-color: #333333;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
}
