.hero {
  display: flex;
  align-items: center;
  width: 1170px;
  margin: 0 auto;
  margin-bottom: 90px;
  position: relative;
}

.hero__descr {
  padding-right: 9%;
}

.hero__title {
  font-size: 64px;
  line-height: 64px;
  font-weight: 300;
  color: #bdbdbd;
  text-transform: uppercase;
  margin: 0;

}

.hero__subtitle {
  font-size: 64px;
  line-height: 64px;
  font-weight: 700;
  color: #333333;
  margin: 0;
}

.hero__slider {
  display: flex;
  position: relative;
  margin-right: 9%;
  overflow: hidden;
  width: 770px;
}

.hero__slide {
  width: 770px;
  height: 829px;
}

.hero__prev {
  position: absolute;
  top: 580px;
  left: 0;
  z-index: 5;
}

.hero__next {
  position: absolute;
  top: 580px;
  left: 7%;
  z-index: 5;
}

.hero__counter {
  position: absolute;
  top: 720px;
  left: 0;
  z-index: 5;
  font-size: 24px;
  line-height: 24px;
  color: #bdbdbd;
}

.hero__counter span {
  padding: 0 10px;
}

.hero__wrapper {
  position: relative;
  display: flex;
}

.hero__view {
  position: absolute;
  display: flex;
  align-items: center;
  width: 220px;
  height: 70px;
  background-color: #ffffff;
  bottom: 0;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 20%;
  text-transform: uppercase;
}

.hero__link {
  color: #333333;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 220px;
  margin-left: 20px;
  margin-right: 50px;
}

.about {
  display: flex;
  margin: 0 auto;
  width: 1170px;
  background-color: #fbfbfb;
  margin-bottom: 30px;
}

.about__images {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 110px;
  gap: 30px;
  padding-left: 100px;
  padding-top: 30px;
  padding-right: 30px;
}

.about__images-3 {
  grid-column: 2 / -1;
  grid-row: 1 / -1;
  align-self: center;
}

.about__descr {
  width: 400px;
}

.about__title {
  font-size: 64px;
  line-height: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
  margin-top: 30px;
}

.about__text {
  font-size: 16px;
  line-height: 25px;
  font-weight: 300;
}

.about__button {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 220px;
  height: 70px;
  background-color: #ffffff;
  color: #333333;
  text-transform: uppercase;
}

.about__button:hover {
  box-shadow: 0 0 5px #000000;
}

.mission {
  padding-top: 120px;
  width: 1170px;
  margin: 0 auto;
}

.mission__title {
  font-size: 64px;
  line-height: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
}

.mission__wrapper {
  display: flex;
  justify-content: space-between;
}

.mission__first {
  display: flex;
  width: 413px;
}

.mission__second {
  display: flex;
  width: 570px;
}

.mission__number {
  font-size: 200px;
  font-weight: 900;
  color: #f2f2f2;
  margin-right: 30px;
}

.mission__text {
  font-size: 22px;
  line-height: 36px;
  font-weight: 400;
  margin-top: 40px;
}

.projects {
  width: 1170px;
  margin: 0 auto;
}

.projects__title {
  font-size: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
  margin-top: 100px;
  margin-bottom: 60px;
}

.projects__samples {
  display: grid;
  grid-template-columns: 270px 270px 1fr 370px;
  grid-template-rows: 1fr 1fr 70px;;
  gap: 30px;
  grid-template-areas: "a a b b"
                       "c d d e"
                       "0 0 0 f";
}

.samples__button {
  grid-area: a;
  background: linear-gradient(to top, rgba(51,51,51,0.8), rgba(51,51,51,0.8)),
              url('/img/sample-1.jpg');
  font-size: 64px;
  font-weight: 700;
  color: #ffffff;
  text-align: left;
  padding-left: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.samples__button:hover {
  opacity: 0.5;
}

.samples__link {
  font-size: 12px;
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  width: 130px;
}

.samples__item-2 {
  grid-area: b;
}

.samples__item-3 {
  grid-area: c;
}

.samples__item-4 {
  grid-area: d;
}

.samples__item-5 {
  grid-area: e;
}

.projects__button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-area: f;
  width: 220px;
  background-color: #333333;
  justify-self: right;
  font-size: 12px;
  font-weight: 400;
  color: #ffffff;
  padding-left: 40px;
  padding-right: 70px;
  text-transform: uppercase;
}

.projects__button:hover {
  opacity: 0.7;
}

.contact-us {
  max-width: 1170px;
  margin: 0 auto;
  margin-top: 120px;
  margin-bottom: 120px;
}

.contact-us__title {
  font-size: 64px;
  font-weight: 300;
  margin: 0;
  margin-bottom: 22px;
  color: #bdbdbd;
}

.contact-us__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.contact-us__form {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 390px;
}

.contact-us__item {
  height: 45px;
  margin-bottom: 10px;
  border: none;
  background-color: #f3f3f3;
  padding-left: 21px;
  font-size: 14px;
  letter-spacing: 4%;
}

.contact-us__item--textarea {
  height: 150px;
  padding-top: 14px;
}

.contact-us__button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 77px;
  width: 220px;
  height: 70px;
  background-color: #333333;
  color: #ffffff;
  padding-left: 40px;
  padding-right: 50px;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 20%;
}

.contact-us__button:hover {
  opacity: 0.7;
}
