.photo-gallery {
  max-width: 1170px;
  margin: 0 auto;
}

.photo-gallery__title {
  font-size: 64px;
  font-weight: 300;
  color: #bdbdbd;
  margin: 0;
  margin-top: 63px;
}

.photo-gallery__subtitle {
  font-size: 64px;
  font-weight: 700;
  color: #333333;
  margin: 0;
  margin-bottom: 30px;
}

.photo-gallery__slider {
  padding-top: 30px;
  margin-bottom: 120px;
  border-top: 1px solid #f2f2f2;
  position: relative;
}

.photo-gallery__wrapper {
  overflow: hidden;
  display: flex;
}

.photo-gallery__slide {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
}

.photo-gallery__controls {
  margin-top: 60px;
  display: flex;
  align-items: center;;
}

.photo-gallery__counter {
  font-size: 24px;
  font-weight: 400;
  color: #bdbdbd;
}

.photo-gallery__current-slide {
  padding-right: 30px;
}

.photo-gallery__all-slides {
  padding-left: 30px;
}

.photo-gallery__prev {
  padding-left: 30px;
}

.photo-gallery__next {
  padding-left: 20px;
}

